import request from '../utils/request'
export function notice(params) {
    return request({
        url: '/api/system/notice/list?noticeType=1',
        method: 'GET',
        params
    })
}
export function notice2(params) {
    return request({
        url: '/api/system/notice/list?noticeType=2',
        method: 'GET',
        params
    })
}
export function addy(data) {
    return request({
        url: '/api/ktv/notice',
        method: 'POST',
        data
    })
}
