<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="平台通知" name="0"></el-tab-pane>
    <el-tab-pane label="系统通知" name="1"></el-tab-pane>
  </el-tabs>
</template>

<script>
export default {
  data() {
    return {
      activeName: "0",
    };
  },
  methods: {
    handleClick({name},) {
      this.activeName = name
      console.log(name);
      this.$emit('num',name)
    },
  },
};
</script>

