<template>
  <div class="notices">
    <div class="header">
      <div class="back">
        <img src="../assets/left2.png" alt="" class="back" />
      </div>
    </div>
    <div class="notice">
      <h3 class="h3s">通知管理</h3>
      <el-button class="btn" @click="add">新增</el-button>
      <tabs class="tabs" @num="fl"></tabs>
      <ul v-show="show == 0" class="content-platform">
        <li v-for="(item, index) in platform" :key="index">
          <div class="time">{{ item.createTime }}</div>
          <div class="btitle">{{ item.noticeTitle }}</div>
          <div
            class="stitle"
            v-html="'<span>内容:</span>' + item.noticeContent"
            ref="pt"
          ></div>
          <span></span>
        </li>
      </ul>
      <ul v-show="show == 1" class="content-system">
        <li v-for="(item, index) in system" :key="index">
          <div class="time">{{ item.createTime }}</div>
          <div class="btitle">{{ item.noticeTitle }}</div>
          <div
            class="stitle"
            v-html="'<span>内容:</span>' + item.noticeContent"
          ></div>
          <span></span>
        </li>
      </ul>
      <div class="btm">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[6, 12, 24, 48]"
          :page-size="pageSize"
          layout="sizes, prev, pager, next"
          :total="90"
        >
        </el-pagination>
      </div>
      <div class="add" v-show="adds">
        <div class="topadd">
          <h3>增加信息</h3>
          <img src="../assets/x.png" alt="" @click="close" />
        </div>
        <div class="content">
          <div class="left">
            <p>消息类型<span>*</span></p>
            <div class="select">
              <p ref="p">{{ choosetitle }}</p>
              <img src="../assets/sanjiao.png" alt="" @click="ul" />
              <ul v-show="uls">
                <li
                  v-for="(itemt, indext) in notice"
                  :key="indext"
                  @click="choose(indext)"
                >
                  {{ itemt.title }}
                </li>
              </ul>
            </div>
          </div>
          <div class="right">
            <p>标题<span>*</span></p>
            <div class="select">
              <p ref="p2">{{ choosetitle2 }}</p>
              <img src="../assets/sanjiao.png" alt="" @click="ul2" />
              <ul v-show="uls2">
                <li
                  v-for="(itemt2, indext2) in notice2"
                  :key="indext2"
                  @click="choose2(indext2)"
                >
                  {{ itemt2.title }}
                </li>
              </ul>
            </div>
          </div>
          <div class="bottoms">
            <p>内容<span>*</span></p>
            <textarea
              class="select"
              v-model="textareadata"
              ref="text"
            ></textarea>
            <div class="styles">
              <div class="H sty" @click="hs">
                H
                <ul v-show="h">
                  <li
                    v-for="(itemh, indexh) in hlist"
                    :key="indexh"
                    v-html="itemh.title"
                    @click="hchoose(indexh)"
                    ref="hxz"
                  ></li>
                </ul>
              </div>
              <div class="B sty" @click="Bs" ref="bbb">B</div>
              <div class="T sty">T</div>
            </div>
          </div>
          <div class="yes" @click="yes">确认添加</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tabs from "@/components/tabs.vue";
import { notice } from "../api/notice";
import { notice2 } from "../api/notice";
import { addy } from "../api/notice";
export default {
  components: {
    tabs,
  },
  data() {
    return {
      currentPage: 1,
      platform: [],
      system: [],
      notice: [
        {
          title: "平台通知",
          notice2: [{ title: "平台1" }, { title: "平台2" }, { title: "平台3" }],
        },
        {
          title: "系统通知",
          notice2: [{ title: "系统1" }, { title: "系统2" }],
        },
      ],
      notice2: [],
      hlist: [
        { title: "<h1>H1</h1>" },
        { title: "<h2>H2</h2>" },
        { title: "<h3>H3</h3>" },
        { title: "<h4>H4</h4>" },
        { title: "<h5>H5</h5>" },
      ],
      show: 0,
      pageSize: 6,
      adds: false,
      choosetitle: "平台通知",
      choosetitle2: "预约通知",
      uls: false,
      uls2: false,
      textareadata: "",
      textareadata2: "",
      type: "",
      noticeTitle: "",
      h: false,
      bbb: false,
    };
  },
  methods: {
    Bs() {
      if (!this.bbb) {
        this.$refs.text.style.fontWeight = "bolder";
        this.$refs.bbb.style.color = "#1e88e5";
        this.$refs.bbb.style.fontWeight = "bolder";
        this.bbb = !this.bbb;
      } else {
        this.$refs.text.style.fontWeight = "lighter";
        this.$refs.bbb.style.color = "#2c3e50";
        this.$refs.bbb.style.fontWeight = "lighter";
        this.bbb = !this.bbb;
      }
    },
    hchoose(e) {
      console.log(e);
      this.$refs.text.style.fontSize = this.$refs.hxz[e].style.fontSize;
      console.log(this.textareadata);
      this.textareadata2 =
        "<h" + (e + 1) + ">" + this.textareadata + "</h" + (e + 1) + ">";
      console.log(this.textareadata2);
    },
    hs() {
      this.h = !this.h;
    },
    ul() {
      this.uls = !this.uls;
    },
    ul2() {
      this.uls2 = !this.uls2;
    },
    add() {
      this.adds = true;
    },
    close() {
      this.adds = false;
    },
    fl(val) {
      this.show = val;
      notice({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
        console.log(res);
        this.platform = res.rows;
        console.log(this.platform);
      });
      notice2({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
        console.log(res);
        this.system = res.rows;
        console.log(this.platform);
      });
    },
    handleSizeChange(val) {
      this.pageSize = val;
      console.log(this.pageSize);
      notice({ pageNum: this.currentPage, pageSize: this.pageSize }).then(
        (res) => {
          console.log(res);
          this.platform = res.rows;
          console.log(this.platform);
        }
      );
      notice2({ pageNum: this.currentPage, pageSize: this.pageSize }).then(
        (res) => {
          this.system = res.rows;
          console.log(this.system);
        }
      );
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      notice({ pageNum: this.currentPage, pageSize: this.pageSize }).then(
        (res) => {
          console.log(res);
          this.platform = res.rows;
          console.log(this.platform);
        }
      );
      notice2({ pageNum: this.currentPage, pageSize: this.pageSize }).then(
        (res) => {
          this.system = res.rows;
          console.log(this.system);
        }
      );
    },
    choose(e) {
      this.choosetitle = this.notice[e].title;
      this.notice2 = this.notice[e].notice2;
      this.$refs.p.style.color = "#171c1f";
      this.uls = false;
      this.type = e + 1;
      console.log(this.type);
    },
    choose2(e) {
      this.choosetitle2 = this.notice2[e].title;
      this.uls2 = false;
      this.$refs.p2.style.color = "#171c1f";
      if (e == 0) {
        this.noticeTitle = 115;
      } else if (e == 1) {
        this.noticeTitle = 126;
      } else {
        this.noticeTitle = 128;
      }
    },
    yes() {
      this.textareadata2 = this.textareadata
      addy({
        noticeContent: this.textareadata2,
        noticeTitle: this.noticeTitle,
        noticeType: this.type,
        status: 0,
      }).then((res) => {
        console.log(res);
        this.adds = false;
        // dom元素更新后执行，因此这里能正确打印更改之后的值
        this.$nextTick(() => {
          this.$refs.pt[0].style.fontSize = this.$refs.text.style.fontSize;
          console.log(this.$refs.pt[0].style.fontSize);
        });
        notice({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
          this.platform = res.rows;
          this.$nextTick(() => {
            this.$refs.pt[0].style.fontSize = this.$refs.text.style.fontSize;
            console.log(this.$refs.pt[0].style.fontSize);
          });
          // this.$refs.pt.style.fontSize = this.$refs.text.style.fontSize
        });
        notice2({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
          this.system = res.rows;
        });
      });
    },
  },
  watch: {},
  mounted() {
    notice({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
      this.platform = res.rows;
    });
    notice2({ pageNum: 1, pageSize: this.pageSize }).then((res) => {
      this.system = res.rows;
    });
    this.$nextTick(() => {
      this.$refs.hxz[0].style.fontSize = "30px";
      this.$refs.hxz[1].style.fontSize = "20px";
      this.$refs.hxz[2].style.fontSize = "18px";
      this.$refs.hxz[3].style.fontSize = "16px";
      this.$refs.hxz[4].style.fontSize = "13px";
    });
  },
};
</script>

